.order {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.order__number {
  text-shadow: 0px 0px 16px rgba(51, 51, 255, 0.25),
    0px 0px 8px rgba(51, 51, 255, 0.25), 0px 4px 32px rgba(51, 51, 255, 0.5);
}
.order__image {
  width: 120px;
  height: 120px;
}
